rect.selection {
    stroke: #545454;
    stroke-width: 2px;
    stroke-opacity: 1;
    fill: #bdcadb33;
    fill-opacity: 0.5;
  }
  .bar rect {
    fill: #999999b3;
    rx:2;
  }
  .bar text {
    fill: #000000;
    font: 10px sans-serif;
  }
  .hist_title{
    fill: #000000;
    font: 13px sans-serif;
  }
  .histogram .tick text{
    fill: #000000;
  }
  .histogram_div{
    padding: 0px!important;
    background-color: rgba(233, 233, 233, 0.1);
    border-bottom: 1px solid rgb(175, 175, 175);
  }
  .cat_item_clicked{
    fill: rgb(151, 165, 153)!important;
  }

.slopechart{
  overflow-y: hidden;
}
.slopechart_col{
  background-color: white!important;  
  overflow: hidden;
}
.range_chart_col{
  overflow-y: hidden;
  background-color: white!important;
}
body {
  font-family: 'Open Sans', sans-serif;
}
.Parent{
  display: inline-block;
  margin-left: 50px;
}
.title {
  font-size: 15px;
  font-weight: 700;
}

.slope-line {
  stroke-width: 1px;
  stroke-linecap: round;
}
.slope-label-left, .slope-label-right {
  font-size: 12px;
  cursor: default;
  font-weight: 400;
}
.slopechart_text{
  font-size: 12px;
  cursor: default;
  font-weight: 400;
}
.label-figure {
  font-weight: 400;
}

.border-lines {
  stroke: rgb(0, 0, 0);
  stroke-width: .1px;
}

.voronoi path {
  fill: none;
  pointer-events: all;
}

.slope_chart_title{
  display: flex; justify-content:  space-between;
  padding: 0px 10px;
  font-weight: 500;
  font-size: 13px;
  color: rgb(0, 0, 0);
  margin-top:0px ;
  height: 25px;
}
.model_score{
  display: flex; 
  justify-content:  center;
  padding: 0px 10px;
  font-weight: 500;
  font-size: 13px;
  color: rgb(0, 0, 0);
  margin-top:0px ;
  height: 25px;
  background-color: #fafafa;
  
}
.model_title{
  margin-left: 30px;
  color: rgb(0, 0, 0);
}
text{
stroke:none!important;
}
div.tooltip {	
  position: absolute;			
  text-align: center;			
  width: 140px;					
  height: auto;					
  padding: 8px 8px;				
  font: 14px sans-serif;		
  background: rgb(0, 0, 0);	
  border: 0px;		
  border-radius: 3px;			
  pointer-events: none;
  font-weight: 500;
  color: white;			
}
