.my-selector{
  font-weight: bold;
  fill-opacity: 1;
  opacity: 1;
  stroke-width: 1;
  }
.reducedopacity{
  fill-opacity: .3;
  opacity: .3;
}
.slope-group line{
  stroke-width: 2;  
}
.highlighted{
  fill-opacity: 1!important;
  opacity: 1!important;
  font-weight: bold;
}
.highlighted_line{
  stroke-width: 4!important;
  fill-opacity: 1!important;
  opacity: 1!important;
}
.not_avail{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.legend p{
  font-size: 12px;
  margin: 0;
  padding: 5px 0px;
  margin-left: 1px;
}
div.tooltip {	
  position: absolute;			
  text-align: center;								
  padding: 10px 2px;				
  font: 12px sans-serif;		
  background: rgb(0, 0, 0);	
  border: 0px;		
  border-radius: 8px;			
  pointer-events: none;			
}
